import React ,{Component, Fragment} from 'react';
import CustomersComponent from './components/optionals/customers';
import FeaturesComponent from './components/optionals/features';
import GaleryComponent from './components/optionals/galery';
import GetStartedComponent from './components/optionals/getStarted';
import WeWorkComponent from './components/optionals/howWeWork';
import PorfolioComponent from './components/optionals/porfolio';
import TemporaryDrawer from './views/sideBar';
import ContactUsView from './views/contactUs';
import Holder from './views/holder';
import NavBar from './views/navBar';
import { ContactUs } from './interface';
import { defaultValue } from './helper';

class LadingPage1 extends Component<any,any>{
    constructor(props:any) {
        super(props)
        this.state={
            title:"Create Professional Looking Website",
            holder:"1",
            contactUsNumber:"1",
            contactUs:{
                title:"Questions?",subTitle:"Lorem ipsum dolor sit amet, consectetuer adipiscing elit, sed diam nonummy nibh euismod tincidunt ",
                mail:"info@gmaiil.com",phoneNumber:"1165336844",textButton:"Send Now"
            } as ContactUs,
            subTitle:"With latest design trends and technologies ",
            textButton:"Start Free Now",
            navBar:["Products","About us","Help"],
            portfolio:false,
            portfolioList: defaultValue("portfolio"),
            feature:false,
            featureList:defaultValue("Features"),
            getStaterd:false,
            getStaterdList:defaultValue("Get started"),
            howWeWork:false,
            howWeWorkList: defaultValue("How we work"),
            customers:false,
            customersList: null,
            galery:false,
            galeryList: defaultValue("gallery"),
            portFolioTitle:null,
            getStaterdTitle: null,
            howWeWorkTitle:null,
            galeryTitle: null,
        }
    }

    componentDidMount= async()=>{
        await this.buildLocalStorage();
    }
    
    handleInfoHolder = (holder:any)=>{
        const{title,subTitle,textButton}=holder;
        this.setState({...this.state,title,subTitle,textButton})
    }

    buildLocalStorage = ()=>{    
        localStorage.clear();
        localStorage.setItem('obj', JSON.stringify(this.state));
    }

    handleChangeNavBar = (navBar:any[])=>{
        this.setState({...this.state,navBar},()=>{
            this.buildLocalStorage();
        })
    }
    handleChange = (editor:string,value:any)=>{
        this.setState({...this.state,[`${editor}`]:value},()=>{
            this.buildLocalStorage();
        })
    }
    handleChangeFlag = (editor:string,value:any)=>{
        this.setState({...this.state,[`${editor}`]:value},()=>{
            this.buildLocalStorage();
        })
    }
    render(){
        const{title,subTitle,textButton,holder,
            customers,getStaterd,howWeWork,portfolio,galery,howWeWorkList,
            feature,navBar,contactUsNumber,contactUs,featureList,galeryList}=this.state;
        return(
            <Fragment>
                <div className="sideBar-button">
                    {
                        <TemporaryDrawer
                            Holder={(holder:string)=>this.setState({...this.state,holder})}
                            sendHolder={(holder:any)=>this.handleInfoHolder(holder)}
                            ContactUs={(contactUsNumber:any)=>this.setState({...this.state,contactUsNumber})}
                            sendContactUs={(contactUs:ContactUs)=>this.setState({...this.state,contactUs})}
                            sendNavBar={(navBar:any)=>this.handleChangeNavBar(navBar)}
                            generalEditor={(list:string,howWeWorkList:any)=>this.handleChange(list,howWeWorkList)}
                            generalFlag={(list:string,flag:any)=>this.handleChangeFlag(list,flag)}
                        />
                    }
                </div>
                <NavBar
                    navBar={navBar}
                />
                <div id="content" className="site-content content-wrapper page-content">
			        <div className="page type-page hentry">
				        <div className="page-content-body">
                            <Holder 
                                holder={holder}
                                title={title}
                                subTitle={subTitle}
                                textButton={textButton}
                            />
                            {customers && (<CustomersComponent/>)}
                            {getStaterd && (<GetStartedComponent/>)}
                            {howWeWork && (<WeWorkComponent howWeWorkList={howWeWorkList}/>)}
                            {portfolio && (<PorfolioComponent/>)}
                            {galery &&(<GaleryComponent galeryList={galeryList}/>)}
                            {feature &&(<FeaturesComponent featureList={featureList}/>)}
                            <ContactUsView contactUsNumber={contactUsNumber} contactUs={contactUs}/>
                            
                        </div>
                    </div>
                </div>
            </Fragment>
        )
    }
}

export default LadingPage1;