import React, { Fragment } from 'react';


const ContactUs1 = (props:any)=>{
    return(
        <Fragment>
            <section className="questions">
                <div className="container">
                    <div className="row">
                        <div className="col-sm-6">
                            <img src="../../../assets/images/question-img.jpg" alt="images"/>
                        </div>
                        <div className="col-sm-6">
                            <h2>{props.contactUs.title}</h2>
                                <p>{props.contactUs.subTitle}</p>
                            <ul className="contact-details">
                                <li>
                                    <a href="tel:14952867060">{props.contactUs.phoneNumber}</a>
                                </li>
                                <li>
                                    <a href="mailto:info@email.com">{props.contactUs.mail}</a>
                                </li>
                            </ul>
                            <div className="question-form">
                                <div className="row">
                                    <div className="col-sm-5">
                                        <input type="text" placeholder="Your Name"className="form-control" />
                                    </div>
                                    <div className="col-sm-7">
                                        <div className="phone-number">
                                            <select className="selectpicker">
                                                <option>+63</option>
                                                <option>+64</option>
                                                <option>+65</option>
                                            </select>
                                            <input type="text" placeholder="000 000 000"className="form-control" />
                                        </div>
                                    </div>
                                </div>
                                <div className="row">
                                    <div className="col-sm-12">
                                        <textarea placeholder="Your Message"className="form-control" ></textarea>
                                    </div>
                                </div>
                                <div className="row">
                                    <div className="col-sm-12">
                                        <input type="submit" value={props.contactUs.textButton} className="primary-link hvr-push"/>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </section>

        </Fragment>
    )
}

export default ContactUs1;