import React ,{Component, Fragment} from 'react';

class Holder1 extends Component<any>{
    constructor(props:any) {
        super(props)
    }
    render(){
        const{title,subTitle,textButton,holder}=this.props;
        return(
            <Fragment>
                	<section className="hero-1">
						<div className="container">
							<div className="row">
								<div className="col-sm-6">
									<div className="hero-content">
										<h1>{title}</h1>
                                        <a href="#" className="primary-link hvr-push">{textButton}</a>
									</div>
								</div>
								<div className="col-sm-6">
									<img src="../../../assets/images/hero-img-1.png" alt="image"/>
								</div>
							</div>
						</div>
					</section>
            </Fragment>
        )
    }
}

export default Holder1;