import React from 'react';
import clsx from 'clsx';
import { makeStyles } from '@material-ui/core/styles';
import Drawer from '@material-ui/core/Drawer';
import Button from '@material-ui/core/Button';
import List from '@material-ui/core/List';
import Divider from '@material-ui/core/Divider';
import ListItem from '@material-ui/core/ListItem';
import AccordionCustom from '../components/controller/accordion';
import { Holder } from '../interface';



const useStyles = makeStyles({
  list: {
    width: 250,
  },
  fullList: {
    width: 'auto',
  },
});

type Anchor = 'top' | 'left' | 'bottom' | 'right';

 const TemporaryDrawer = (props:any) =>{
  const classes = useStyles();
  const [state, setState] = React.useState({
    top: false,
    left: false,
    bottom: false,
    right: false,
  });

  const toggleDrawer = (anchor: Anchor, open: boolean) => (
    event: React.KeyboardEvent | React.MouseEvent,
  ) => {
    if (
      event.type === 'keydown' &&
      ((event as React.KeyboardEvent).key === 'Tab' ||
        (event as React.KeyboardEvent).key === 'Shift')
    ) {
      return;
    }

    setState({ ...state, [anchor]: open });
  };

  const list = (anchor: Anchor) => (
    <div
      className={clsx(classes.list, {
        [classes.fullList]: anchor === 'top' || anchor === 'bottom',
      })}
      role="presentation"
    >
      <List>
        {['Holder', 'Contact Us', 'Nav Bar'].map((text, index) => (
          <ListItem button key={text}>
            <AccordionCustom 
              name={text}
              Holder={(value:any)=>{props.Holder(value)}}
              sendHolder={(value:Holder)=>{props.sendHolder(value)}}
              ContactUs={(value:string)=>props.ContactUs(value)}
              sendContactUs={(value:any)=>props.sendContactUs(value)}
              sendNavBar={(value:any)=>props.sendNavBar(value)}
            />
          </ListItem>
        ))}
      </List>
      <Divider />
      <div>
        <h2>Optionals</h2>
      </div>
      <List>
        {['Get started', 'How we work', 'Gallery','Features','Customers','Porfolio'].map((text, index) => (
          <ListItem button key={text}>
            <AccordionCustom 
              name={text}
              GaleryEditor={(value:boolean)=>props.GaleryEditor(value)}
              generalFlag={(list:string,flag:any)=>props.generalFlag(list,flag)}
              generalEditor={(list:string,value:any)=>props.generalEditor(list,value)}
            />
          </ListItem>
        ))}
      </List>
    </div>
  );

  return (
    <div>
        <React.Fragment key={"left"}>
          <Button onClick={toggleDrawer("left", true)}>Configurations</Button>
          <Drawer anchor={"left"} open={state["left"]} onClose={toggleDrawer("left", false)}>
            {list("left")}
          </Drawer>
        </React.Fragment>

    </div>
  );
}

export default TemporaryDrawer;