import { Box, Button, Grid, TextField } from '@material-ui/core';
import React, { Component, Fragment, useEffect, useState } from 'react';


class NavBarEditor extends Component <any,any>{
    constructor(props:any){
        super(props);
        this.state = {
            items :["Products","About Us", "Help"],
            render:false,
            newValue:""
        }
    }

    componentDidMount = ()=>{
        const local:any = localStorage.getItem("obj")
        const obj:any = JSON.parse(local);
        console.log(obj)
        this.setState({...this.state,items:obj.navBar},()=>{
            this.buildState();
        });
    }



    buildState = ()=>{
        const {items}=this.state;
        items.forEach((e:any,i:any) => {
            this.setState({...this.state,[`${i}`]:e,render:true}); 
        });
    }

    handleChange = (e:any)=>{
        const { id , value} = e.target;
        const {items}=this.state;
        let newItems:any[] = items;
        newItems[id]=value;
        this.setState({...this.state,[`${id}`]:value,items:newItems}); 
    }

    handleChangeNewItem = (e:any)=>{
        const { value} = e.target;
        this.setState({...this.state,newValue:value},()=>console.log(this.state)); 
    }

    deleteItem = (item:any)=>{
        const {items}=this.state;
        const newItems = items.filter((it:any)=> it !== item);
        this.setState({...this.state,items:newItems}); 
    }

    addItem =()=>{
        const {items,newValue}=this.state;
        let newItems:any[] = items;
        newItems.push(newValue);
        this.setState({...this.state,items:newItems}); 
    }

    sendNavBar = ()=>{
        this.props.sendNavBar(this.state.items)
    }

    render(){
        const{items,render,newValue}=this.state;
        return(
            <Fragment>
                <Box display="flex" flexDirection="column" style={{width:"100%"}}>
                    <Grid container spacing={1}>
                        <Grid container item xs={12} spacing={3}>
                            { render && (   
                                items.map((element:any,i:any)=>{
                                return(
                                    <Box display="flex" p={1} justifyContent="center">
                                        <Box>
                                            <TextField 
                                                id={i} 
                                                label={element} 
                                                variant="outlined" 
                                                onChange={(e)=>this.handleChange(e)}
                                                placeholder={element}
                                                value={this.state.i}
                                            />
                                        </Box>
                                        <Box display="flex">
                                            <Button  color="secondary" style={{width:"20%"}} onClick={()=>this.deleteItem(element)} >
                                                Delete
                                            </Button> 
                                        </Box>
                                    </Box>
                                )
                            }))}
                        </Grid>
                    </Grid>
                    <Grid container spacing={1} style={{paddingTop:"20px"}}>
                        <Grid container item xs={12} spacing={3}>
                            <Box display="flex" p={1} justifyContent="center">
                                <Box>
                                    <TextField 
                                        label={"Add new item"} 
                                        variant="outlined" 
                                        onChange={(e)=>this.handleChangeNewItem(e)}
                                        placeholder={"Add new item"} 
                                        value={newValue}
                                    />
                                </Box>
                                <Box display="flex">
                                    <Button  color="primary" style={{width:"20%"}} onClick={()=>this.addItem()} >
                                        Add
                                    </Button> 
                                </Box>
                            </Box>
                        </Grid>
                    </Grid>
                    <Grid container spacing={1} style={{paddingTop:"20px"}}>
                        <Grid container item xs={12} spacing={3}>
                            <Button variant="outlined" color="primary" onClick={()=>this.sendNavBar()}style={{width:"100%"}}>
                                Save
                            </Button>
                        </Grid>
                    </Grid>
                </Box>
            </Fragment>
        )
    }
}



export default NavBarEditor;