import React, { Fragment } from 'react';
import Holder1 from '../components/holders/holder1';
import Holder2 from '../components/holders/holder2';
import Holder3 from '../components/holders/holder3';

const Holder = (props:any)=>{

    const renderHolder = ()=>{
        const{title,subTitle,textButton,holder}=props;
        if (holder) {
            switch (holder) {
                case "1":return <Holder1 title={title} subTitle={subTitle} textButton={textButton}/>
                case "2":return <Holder2 title={title} subTitle={subTitle} textButton={textButton}/>
                case "3":return <Holder3 title={title} subTitle={subTitle} textButton={textButton}/>
                default:
                break;
            }
        }
    }

    return(
        <Fragment>
            {renderHolder()}
        </Fragment>
    )
}

export default Holder;