import React, { Fragment } from 'react';


const WeWorkComponent = (props:any)=>{
    return(
        <Fragment>
        <section className="how-we-work">
            <div className="container">
                <div className="section-header">
                    <h2>How We Do It</h2>
                </div>
                <div className="section-content">
                    <div className="row">
                        {
                            props.howWeWorkList && (
                            props.howWeWorkList.map((w:any)=>{
                                return(
                                    <div className="col-sm-6 col-md-3">
                                        <div className="steps">
                                            <div className="step-icon">
                                                <span>1</span>
                                                <i className={w.icon}></i>
                                            </div>
                                            <h3>{w.title}</h3>
                                            <p>{w.subTitle}</p>
                                        </div>
                                    </div>
                                )
                            }))
                        }
                    </div>
                </div>
                <div className="section-footer">
                    <a href="#" className="primary-link-2 hvr-push">Create Now</a>
                </div>
            </div>
        </section>

        </Fragment>
    )
}

export default WeWorkComponent;