import { Box, Button, FormLabel, Grid, Radio, TextField } from '@material-ui/core';
import React, { Fragment } from 'react';
import { ContactUs } from '../../interface';
import UpLoadImages from '../upLoadImages';

const ContactUsEditor = (props:any)=>{

    const [selectedValue, setSelectedValue] = React.useState('1');
    const [title, setTitle] = React.useState('');
    const [subTitle, setSubTitle] = React.useState('');
    const [textButton, setTextButton] = React.useState('');
    const [phoneNumber, setPhoneNumber] = React.useState('');
    const [mail, setMail] = React.useState('');
    const ContactUsList =["1","2"]

    const handleChange = (e:any)=>{
        setSelectedValue(e.target.value);
        props.ContactUs(e.target.value)
    }

    const sendContactUs = ()=>{
        let conctacUs:ContactUs ={
            title,subTitle,mail,phoneNumber,img:null,textButton
        }
        props.sendContactUs(conctacUs);
    }
    return(
        <Fragment>
            <Box display="flex" flexDirection="column" style={{width:"100%"}}>
                <Box display="flex" p={1} justifyContent="space-between" >
                    <div style={{marginRight:"20px"}} >
                        {
                            ContactUsList.map((element:any)=>{
                                return(
                                    <div>
                                        <FormLabel component="legend">{`ContactUs${element}`}</FormLabel>
                                        <Radio
                                            onChange={(e)=>handleChange(e)}
                                            value={element}
                                            name="radio-button-demo"
                                            inputProps={{ 'aria-label': 'A' }}
                                        />
                                    </div>
                                )
                            })
                        }
                    </div>
                    <Grid container spacing={1} >
                        <Grid container item xs={12} spacing={4} >
                            <Box>
                                <UpLoadImages/>
                            </Box>
                            <TextField id="title" label="Title" variant="outlined" value={title} onChange={(e:any)=>setTitle(e.target.value)} style={{paddingTop:"10px"}}/>
                            <TextField id="subTitle" label="Sub Title" variant="outlined"  value={subTitle} onChange={(e:any)=>setSubTitle(e.target.value)} style={{paddingTop:"10px"}}/>
                            <TextField id="textButton" label="Text Button" variant="outlined" value={textButton}  onChange={(e:any)=>setTextButton(e.target.value)} style={{paddingTop:"10px"}}/>
                            <TextField id="phoneNumber" label="Phone Number" variant="outlined" value={phoneNumber} onChange={(e:any)=>setPhoneNumber(e.target.value)}  style={{paddingTop:"10px"}}/>
                            <TextField id="mail" label="Email" variant="outlined" value={mail} onChange={(e:any)=>setMail(e.target.value)} style={{paddingTop:"10px"}} />
                        </Grid>
                    </Grid>
                </Box>
                <Box display="flex" p={1} justifyContent="center" style={{paddingTop:"20px"}}>
                    <Button variant="outlined" color="primary" onClick={()=>sendContactUs()}style={{width:"100%"}}>
                        Save
                    </Button>
                </Box>
            </Box>
        </Fragment>
    )
}

export default ContactUsEditor;