import React, { Fragment } from 'react';
import { PortfolioObj } from '../../interface';

const GaleryComponent = (props:any)=>{
    return(
        <Fragment>
            <section className="our-offices">
                <div className="container">
                    <div className="section-header">
                        <h2>Our Offices</h2>
                    </div>
                    <div className="section-content">
                        <div className="row">
                            {
                                props.galeryList && (
                                    props.galeryList.map((g:PortfolioObj)=>{
                                        return(
                                        <div className="col-md-6">
                                            <div className="office-item">
                                                <img src="../../../assets/images/our-offices-img-1.jpg" alt="image" />
                                                <a href="#" className="office-item-info">
                                                    <div>
                                                        <strong>{g.title}</strong>
                                                       {g.subTitle}
                                                    </div>
                                                </a>
                                            </div>
                                        </div>
                                        )
                                    })
                                )
                            }
                        </div>
                    </div>
                </div>
            </section>
        </Fragment>
    )
}

export default GaleryComponent;