
import React, { Fragment } from 'react';

const GetStartedComponent = (props:any)=>{
    return(
        <Fragment>
            <section className="get-started">
                <div className="container">
                    <div className="section-header">
                        <h2>Let's Get Started</h2>
                    </div>
                    <div className="section-content">
                        <div className="row">
                            <div className="col-sm-6 col-md-3">
                                <div className="steps">
                                    <div className="step-icon">
                                        <span>1</span>
                                        <i className="flaticon-computer-1"></i>
                                    </div>
                                    <h3>Sign Up</h3>
                                    <p>Sign up / register here for free to create your account</p>
                                </div>
                            </div>
                            <div className="col-sm-6 col-md-3">
                                <div className="steps">
                                    <div className="step-icon">
                                        <span>2</span>
                                        <i className="flaticon-web"></i>
                                    </div>
                                    <h3>Choose</h3>
                                    <p>Choose prebuilt website that fits your needs</p>
                                </div>
                            </div>
                            <div className="col-sm-6 col-md-3">
                                <div className="steps">
                                    <div className="step-icon">
                                        <span>3</span>
                                        <i className="flaticon-web-1"></i>
                                    </div>
                                    <h3>Customize</h3>
                                    <p>Customize your website or add texts, images etc</p>
                                </div>
                            </div>
                            <div className="col-sm-6 col-md-3">
                                <div className="steps">
                                    <div className="step-icon">
                                        <span>4</span>
                                        <i className="flaticon-computer"></i>
                                    </div>
                                    <h3>Share</h3>
                                    <p>Share your professional looking website with the world</p>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
        </Fragment>
    )
}

export default GetStartedComponent;