import { Box, Button, FormLabel, Grid, Radio, TextField } from '@material-ui/core';
import React, { Fragment } from 'react';
import { Holder } from '../../interface';


const HolderEditor = (props:any)=>{
    const [selectedValue, setSelectedValue] = React.useState('1');
    const [title, setTitle] = React.useState('');
    const [subTitle, setSubTitle] = React.useState('');
    const [textButton, setTextButton] = React.useState('');

    const HolderList =["1","2","3"]
    const handleChange = (e:any)=>{
        console.log()
        setSelectedValue(e.target.value);
        props.Holder(e.target.value)
    }

    const sendHolder = ()=>{
        const holder:Holder={
            title,
            subTitle,
            textButton,
        }
        props.sendHolder(holder);
    }
    return(
        <Fragment>
            <Box display="flex" flexDirection="column" style={{width:"100%"}}>
                <Box display="flex" p={1} justifyContent="space-between"  >
                    <div style={{marginRight:"20px"}} >
                        {
                            HolderList.map((element:any)=>{
                                return(
                                    <div>
                                        <FormLabel component="legend">{`Holder${element}`}</FormLabel>
                                        <Radio
                                            checked={selectedValue === element}
                                            onChange={(e)=>handleChange(e)}
                                            value={element}
                                            name="radio-button-demo"
                                            inputProps={{ 'aria-label': 'A' }}
                                        />
                                    </div>
                                )
                            })
                        }
                    </div>
                    <Grid container spacing={1}>
                        <Grid container item xs={12} spacing={3}>
                        <TextField id="title" label="Title" variant="outlined" value={title} onChange={(e:any)=>setTitle(e.target.value)} />
                        <TextField id="subTitle" label="Sub Title" variant="outlined"  value={subTitle} onChange={(e:any)=>setSubTitle(e.target.value)}/>
                        <TextField id="textButton" label="Text Button" variant="outlined" value={textButton}  onChange={(e:any)=>setTextButton(e.target.value)}/>
                        </Grid>
                    </Grid>
                </Box>
                <Box display="flex" p={1} justifyContent="center">
                    <Button variant="outlined" color="primary" onClick={()=>sendHolder()}style={{width:"80%"}}>
                        Save
                    </Button>
                </Box>
            </Box>
        </Fragment>
    )
}

export default HolderEditor;