

import { Accordion, AccordionActions, AccordionDetails, AccordionSummary, Button, Divider, Typography } from '@material-ui/core';
import React, { Fragment } from 'react';
import { Theme, createStyles, makeStyles } from '@material-ui/core/styles';
import HolderEditor from './holderEditor';
import GeneralEditor from './GeneralEditor';
import FeatureEditor from './featuresEditor';
import ContactUsEditor from './contactUsEditor';
import NavBarEditor from './navBarEditor';


const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      width: '100%',
    },
    heading: {
      fontSize: theme.typography.pxToRem(15),
    },
    secondaryHeading: {
      fontSize: theme.typography.pxToRem(15),
      color: theme.palette.text.secondary,
    },
    icon: {
      verticalAlign: 'bottom',
      height: 20,
      width: 20,
    },
    details: {
      alignItems: 'center',
    },
    column: {
      flexBasis: '33.33%',
    },
    helper: {
      borderLeft: `2px solid ${theme.palette.divider}`,
      padding: theme.spacing(1, 2),
    },
    link: {
      color: theme.palette.primary.main,
      textDecoration: 'none',
      '&:hover': {
        textDecoration: 'underline',
      },
    },
  }),
);


const AccordionCustom = (props:any)=>{
    const classes = useStyles();

    const renderEditor =()=>{
      switch (props.name) {
        case "Holder": return(<HolderEditor Holder={(value:string)=>props.Holder(value)} sendHolder={(value:any)=>props.sendHolder(value)}/>)
        case "Contact Us":return(<ContactUsEditor ContactUs={(value:string)=>props.ContactUs(value)} sendContactUs={(value:any)=>props.sendContactUs(value)}/>)
        case "Nav Bar":return(<NavBarEditor sendNavBar={(value:any)=>props.sendNavBar(value)}/>)
        case "How we work":return(
          <GeneralEditor 
            list={"howWeWorkList"}
            editor="howWeWork"
            generalFlag={(list:string,flag:any)=>props.generalFlag(list,flag)}
            generalEditor={(list:string,value:any)=>props.generalEditor(list,value)}/>)
        case "Get started":return(
          <GeneralEditor 
            list={"getStaterdList"}
            editor="getStaterd"
            generalEditor={(list:string,value:any)=>props.generalEditor(list,value)}
            generalFlag={(list:string,flag:any)=>props.generalFlag(list,flag)}/>)
        case "Features":return(
          <GeneralEditor 
            list={"featureList"}
            editor="feature"
            generalEditor={(list:string,value:any)=>props.generalEditor(list,value)}
            generalFlag={(list:string,flag:any)=>props.generalFlag(list,flag)}/>)
        case "Porfolio":return(
          <GeneralEditor 
            list={"portfolioList"}
            editor="portfolio"
            generalEditor={(list:string,value:any)=>props.generalEditor(list,value)}
            generalFlag={(list:string,flag:any)=>props.generalFlag(list,flag)}/>)
        case "Gallery":return(
          <GeneralEditor 
            list={"galeryList"}
            editor="galery"
            generalEditor={(list:string,value:any)=>props.generalEditor(list,value)}
            generalFlag={(list:string,flag:any)=>props.generalFlag(list,flag)}/>)
        default:
          break;
      }
    }
    return(
        <Fragment>
            <Accordion >
                <AccordionSummary aria-controls="panel1c-content"id={`${props.name}-panel1c-header`}>
                    <div className={classes.column} style={{width:"100%"}}>
                        <Typography className={classes.heading}>{props.name}</Typography>
                    </div>
                </AccordionSummary>
                <AccordionDetails className={classes.details}>
                    {renderEditor()}
                </AccordionDetails>
                <Divider />
            </Accordion>
        </Fragment>
    )
}

export default AccordionCustom;