import React, { Fragment } from 'react';
import ContactUs1 from '../components/contactUs/contactUs1';
import ContactUs2 from '../components/contactUs/contactUs2';

const ContactUs = (props:any)=>{
    const renderContactUs = ()=>{
        if (props.contactUsNumber) {
            console.log(props.contactUs)
            switch (props.contactUsNumber) {
                case "1":return <ContactUs1 contactUs={props.contactUs}/>
                case "2":return <ContactUs2 contactUs={props.contactUs}/>
                default:
                break;
            }
        }
    }
    return(
        <Fragment>
            {renderContactUs()}
        </Fragment>
    )
}

export default ContactUs;