import React from 'react';

const NavBarComponent  = (props:any)=>{
    return(
        <header id="masthead" className="site-header">
        <div className="container">
            <div className="row">
                <div className="site-branding col-sm-2">
                    <h1 className="site-title">
                        <a  title="Lionic Legal Solution" rel="home">
                            <img src="../../../assets/images/logo.png" alt="logo"/>
                        </a>
                    </h1>
                </div>

                <div className="col-sm-10">
                    <div className="mobile-menu">
                        <ul>
                            <li className="create-now">
                                <a >Create Now</a>
                            </li>
                            <li>
                                <button type="button" className="navbar-toggle collapsed" data-toggle="collapse" data-target="#navbar-mobile">
                                    <span className="sr-only">Toggle navigation</span>
                                    <span className="icon-bar"></span>
                                    <span className="icon-bar"></span>
                                    <span className="icon-bar"></span>
                                </button>
                            </li>
                        </ul>
                    </div>
                    <nav id="site-navigation" className="navbar">
                        <div className="collapse navbar-collapse navbar-ex1-collapse">
                            <ul className="nav navbar-nav navbar-right sf-menu">
                                {
                                    props.navBar.map((nav:string)=>{
                                        return(
                                            <li><a>{nav}</a></li>
                                        )
                                    })
                                }
                                <li className="create-now"><a  className="hvr-push">Create Now</a></li>
                            </ul>
                        </div>
                    </nav>
                </div>
            </div>
        </div>
        <div className="mobile-navigation">
            <div className="container">
                <nav id="site-navigation-mobile" className="navbar">
                    <div id="navbar-mobile" className="navbar-collapse collapse">
                        <ul className="nav navbar-nav">
                            {
                                props.navBar.map((nav:string)=>{
                                    return(
                                        <li><a>{nav}</a></li>
                                    )
                                })
                            }
                        </ul>
                    </div>
                </nav>
            </div>
        </div>
    </header>
    )
}

export default NavBarComponent;