import { Box, Button, Grid, Switch, TextField } from '@material-ui/core';
import React, { Component, Fragment } from 'react';


class GeneralEditor extends Component<any,any>{
    constructor(props:any){
        super(props);
        this.state = {
            items :[],
            switche:false,
            newValue:{title:"",subTitle:"",icon:""}
        }
    }

    componentDidMount= async()=>{
        await this.localStorageChange();
        await this.buildMainForm();
    }

    buildMainForm = ()=>{
        const {items}=this.state;
        items.forEach((e:any,i:any) => {
            this.setState({...this.state,[`${i}`]:e}); 
        });

        let newValue:any = {title:"",subTitle:"",icon:"",img:""};
        if (items[0].img) {
            delete newValue.icon;
        }else{
            delete newValue.img;
        }
        this.setState({...this.state,newValue});
    }

    localStorageChange = ()=>{
        const local:any = localStorage.getItem("obj");
        if (local) {
            let state = JSON.parse(local);
            this.setState({...this.state,items:state[`${this.props.list}`]
            ,switche:state[`${this.props.editor}`]});
        }
    }

    handleChangeSelect=(e:any)=>{
        this.setState({...this.state,switche:e.target.checked});
        this.props.generalFlag(this.props.editor,e.target.checked);
    }

    handleChange = (e:any)=>{
        const {id,name,value}=e.target;
        const {items}=this.state;
        let newItems =items;
        newItems[id][`${name}`] = value;
        let objChange = this.state[id];
        objChange[`${name}`] = value;
        this.setState({...this.state,[`${id}`]:objChange,items:newItems}); 
    }

    deleteItem = (item:any)=>{
        const {items}=this.state;
        const newItems = items.filter((it:any)=> it !== item);
        this.setState({...this.state,items:newItems}); 
    }

    sendData = ()=>{
        const {items}=this.state;
        this.props.generalEditor(this.props.list,items);
    }
    render(){
        const{switche,items}=this.state;
        return(
            <Fragment>
                <Box display="flex" flexDirection="column" style={{width:"100%"}}>
                    <Box display="flex"  p={1} justifyContent="space-around" >
                        <Switch
                            checked={switche}
                            onChange={(e:any)=>this.handleChangeSelect(e)}
                            name="checkedA"
                            inputProps={{ 'aria-label': 'secondary checkbox' }}
                        />
                        <div style={{width:"100%"}}>
                            {   
                                items && (
                                items.length > 0 &&(
                                    items.map((element:any,i:any)=>{
                                        return(
                                            <div style={{width:"100%" ,paddingTop:"30px"}}>
                                                <Grid container spacing={1} style={{width:"100%"}}>
                                                    <Grid container item xs={12} spacing={3} justify="space-between">
                                                        <Box style={{paddingTop:"10px"}}>
                                                            <TextField 
                                                            label={"Title"} 
                                                            variant="outlined" 
                                                            placeholder={element.title}
                                                            name={"title"}
                                                            id={i}
                                                            value={element.title}
                                                            onChange={(e)=>this.handleChange(e)}
                                                            />
                                                        </Box>
                                                        <Box  style={{paddingTop:"10px"}}>
                                                            <TextField 
                                                                label={"Description"} 
                                                                variant="outlined" 
                                                                placeholder={element.subTitle} 
                                                                name={"subTitle"}
                                                                id={i}
                                                                value={element.subTitle}
                                                                onChange={(e)=>this.handleChange(e)}
                                                                />
                                                        </Box>
                                                        {
                                                            element.icon && (
                                                                <Box  style={{paddingTop:"10px"}}>
                                                                    <TextField 
                                                                        label={"Icon's name"} 
                                                                        variant="outlined" 
                                                                        placeholder={element.icon}
                                                                        name={"icon"}
                                                                        id={i}
                                                                        value={element.icon}
                                                                        onChange={(e)=>this.handleChange(e)}
                                                                    />
                                                                </Box>    
                                                            )
                                                        }
                                                        {
                                                            element.img && (
                                                                <Box  style={{paddingTop:"10px"}}>
                                                                    <TextField 
                                                                        label={"Img"} 
                                                                        variant="outlined" 
                                                                        placeholder={element.img}
                                                                        name={"icon"}
                                                                        id={i}
                                                                        value={element.img}
                                                                        onChange={(e)=>this.handleChange(e)}
                                                                    />
                                                                </Box>    
                                                            )
                                                        }
                                                        <Box display="flex" style={{paddingTop:"10px"}}>
                                                            <Button  color="secondary"  onClick={()=>this.deleteItem(element)} >
                                                                Delete
                                                            </Button> 
                                                        </Box>
                                                    </Grid>
                                                </Grid>
                                            </div>
                                        )
                                    }))
                                )
                            }
                            <div style={{width:"100%" ,paddingTop:"40px"}}>
                                <Grid container spacing={1} style={{width:"100%"}} direction="row">
                                    <Grid container item xs={12} spacing={3} justify="space-between" >
                                        <Box>
                                            <TextField label={"Title"} variant="outlined" placeholder={"Title"} />
                                        </Box>
                                        <Box>
                                            <TextField label={"Sub Titile"} variant="outlined" placeholder={"Sub Titile"} />
                                        </Box>
                                        <Box>
                                            <TextField label={"Icon Name"} variant="outlined" placeholder={"Icon Name"} />
                                        </Box>    
                                        <Box display="flex" >
                                            <Button  color="primary" style={{width:"20%"}}  >
                                                Add
                                            </Button> 
                                        </Box>
                                    </Grid>
                                </Grid>
                            </div>

                        </div>

                    </Box>
                    <Box display="flex" p={1} justifyContent="center" style={{paddingTop:"20px"}}>
                        <Button variant="outlined" color="primary" onClick={()=>this.sendData()}style={{width:"100%"}}>
                            Save
                        </Button>
                    </Box>
                </Box>
            </Fragment>
        )
    }
}

export default GeneralEditor;