import React ,{Component, Fragment} from 'react';

class Holder2 extends Component<any>{
    constructor(props:any) {
        super(props)
    }
    render(){
		const{title,subTitle,textButton,holder}=this.props;
        return(
            <Fragment>
                	<section className="hero-2">
						<img src="../../../assets/images/hero-2-bg.jpg" alt="image"/>
						<div className="hero-content-wrapper">
							<div className="container">
								<div className="hero-content">
									<h1>{title}</h1>
									<a href="#" className="primary-link hvr-push">{textButton}</a>
								</div>
							</div>
						</div>
					</section>
            </Fragment>
        )
    }
}

export default Holder2;